import { observable } from "mobx";

export class TodoModel {
    readonly id: number;
    @observable text: string;
    @observable done: boolean = false;
    delete: () => void;

    constructor(id: number, text: string, onDelete: () => void) {
        this.id = id;
        this.text = text;
        this.delete = onDelete;
    }
    toggle = () => {
        this.done = !this.done;
    };
}
